
























































































































/* Reset */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
	-webkit-text-size-adjust: none;
}
textarea, input {
	border: none;
	overflow: auto;
	outline: none;
	resize: none;
	-webkit-box-shadow: none;
	box-shadow: none;
}
*:focus {
	outline: none;
}
a {
	text-decoration: none;
	color: #333;
}
a:visited {
	color: #333;
}
a:hover {
	color: #444;
}
input, textarea {
	-webkit-transition: 0.2s ease-in-out;
	transition: 0.2s ease-in-out;
}

/* Basic */
body {
	font-family: 'Montserrat', sans-serif;
	color: #333;
}
::-webkit-scrollbar {
	width: 8px;
	background-color: rgba(0, 0, 0, 0);
}
::-webkit-scrollbar:hover {
	background-color: rgba(0, 0, 0, 0.09);
}
::-webkit-scrollbar-thumb:vertical {
	background: rgba(0, 0, 0, 0.4);
}
::-webkit-scrollbar-thumb:vertical:active {
	background: rgba(0, 0, 0, 0.61);
}
:focus::-webkit-input-placeholder {
	opacity: 0;
}
.block--blur {
	-webkit-filter: blur(4px);
	        filter: blur(4px);
	pointer-events: none;
}
.none {
	display: none;
}

/* Icon */
.icon {
	text-decoration: none;
	position: relative;
}
.icon:before {
	font-family: FontAwesome;
	font-style: normal;
	font-weight: normal;
	text-transform: none !important;
}
.icon>.label {
	display: none;
}

/* Header */
.header {
	top: 0;
	width: 100%;
	line-height: 80px;
	font-size: 1.7em;
	font-weight: 700;
	color: #333;
	z-index: 10;
	margin-bottom: 1em;
}
.header--container {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	margin: 0 auto;
	padding: 0;
	height: 100%;
	width: 100%;
	max-width: 1200px;
	border-bottom: 5px solid #f1f1f1;
}
.header__btn {
	color: #444;
	font-size: 0.8em;
	border: 2px solid #444;
	text-transform: uppercase;
	line-height: 1.7em;
	text-align: center;
	display: inline-block;
	border-radius: 3px;
	padding: .1em 1em;
	text-decoration: none;
	cursor: pointer;
	-webkit-transition: 0.2s ease-in-out;
	transition: 0.2s ease-in-out;
}
.header__btn:hover {
	color: #fafafa;
	background: #444;
}
.header__btn:active {
	color: #fafafa;
	background: #555;
	border: 2px solid #555;
}

/* Footer */
.footer {
	position: absolute;
	width: 100%;
	bottom: 2%;
	margin-top: 2em;
	height: 2em;
	font-size: 1em;
	font-weight: 700;
}
.footer--container {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	margin: 0 auto;
	max-width: calc(1200px - 2%);
	background: #f1f1f1;
	color: #444;
	padding: 1%;
}
.footer__github {
	margin-left: auto;
	margin-right: 0;
}
.footer__github--img {
	height: 1em;
	width: auto;
}

/* Main */
.main--container {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	margin: 0 auto;
	padding: 0;
	height: 100%;
	width: 100%;
	max-width: 1200px;
	margin-top: 3em;
}

/* Section */
.section {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	margin: 0 auto;
	padding: 0;
