	text-transform: uppercase;
	text-align: center;
	border-radius: 3px;
	text-decoration: none;
	cursor: pointer;
	float: right;
	font-size: 1.5em;
	line-height: 1.25em;
	padding: .75em 1em;
	-webkit-transition: .2s ease-in-out;
