}

/* Latest */
.latest--container {
	width: 100%;
}
.latest--title {
	text-transform: uppercase;
	font-size: 1.5em;
	font-weight: 600;
	text-align: center;
	border-bottom: 5px solid #f1f1f1;
	line-height: 1.5em;
	text-align: center;
	position: relative;
	float: left;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	margin-bottom: 0.25em;
}
.latest--grid {
	display: grid;
	grid-gap: 2em;
	width: 100%;
	grid-template-columns: auto auto auto;
}

/* Filtered */
.filtered--container {
	width: 100%;
}
.filtered--title {
	text-transform: uppercase;
	font-size: 1.5em;
	font-weight: 600;
	border-bottom: 5px solid #f1f1f1;
	line-height: 1.5em;
	position: relative;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	margin-bottom: .25em;
	max-width: 100%;
	max-height: 1.5em;
	overflow: hidden;
	-webkit-transform: translate(0%, -50%);
	        transform: translate(0%, -50%);
}
.filtered--grid {
	display: grid;
	grid-gap: 2em;
	width: 100%;
