}

/* Cards */
.card {
	background: #f5f5f5;
	position: relative;
	width: 100%;
	height: 20em;
	overflow: hidden;
	cursor: pointer;
	border-radius: .5em;
	-webkit-transform: perspective(1px) translateZ(0);
	transform: perspective(1px) translateZ(0);
	-webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0);
	        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
	-webkit-transition-duration: 0.5s;
	transition-duration: 0.5s;
}
.card:hover {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}
.card:active {
	-webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
	transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
}
.card__img {
	height: 100%;
	width: 100%;
	position: absolute;
	-webkit-transform: translateY(-25%);
	        transform: translateY(-25%);
}
.card__container {
	height: 25%;
	width: 90%;
	background: #f5f5f5;
	position: absolute;
	bottom: 15%;
	padding: 5%;
}
.card__title {
	text-align: center;
	font-size: 1.2em;
	font-weight: 600;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	transform: translateX(-50%) translateY(-50%);
	width: 100%;
}
.card__info {
	position: absolute;
	bottom: 0;
	padding: 5%;
	width: 40%;
	height: 5%;
	text-align: center;
	color: #fafafa;
	font-weight: 700;
}
.card * {
	pointer-events: none;
}
.card__loading {
	position: absolute;
	top: 0;
	padding: 15% 45%;
	width: 10%;
}
.card__loading i {
	font-size: 2.5em;
	opacity: 0.4;
	margin: 2%;
}
.info__city {
	background: #333;
}
.info__price {
	background: #444;
	right: 0;
}

/* Modal Info */
.modal--main {
